import Vue from "vue";
import Vuex from "vuex";
import router from "./router";
import axios from "axios";
import { CATEGORY } from "./category";
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    navigationBarExtended: false,
    projects: [],
    categories: CATEGORY,
    currentCategory: CATEGORY.ALL
  },
  mutations: {
    refresh(state, data) {
      state.projects = data;
    },
    setCategory(state, category) {
      state.currentCategory = category;
    },
    toggleNavigationBar(state) {
      state.navigationBarExtended = !state.navigationBarExtended;
    }
  },
  actions: {
    resetCategory(context) {
      context.commit("setCategory", CATEGORY.ALL);
    },
    refresh(context) {
      const jsonUrl =
        router.options.base +
        "data.json" +
        "?v=" +
        process.env.assetsBuildTimeStamp;
      // console.log(jsonUrl);
      axios.get(jsonUrl).then(function(response) {
        context.commit("refresh", response.data);
        router.addRoutes(
          response.data.map((project, index) => {
            return {
              path: "/" + project.url,
              name: project.title + " - " + project.subtitle,
              props: {
                project: project,
                projects: response.data,
                indexOfProject: index
              },
              component: () =>
                import(/* webpackChunkName: "about" */ "./views/Work.vue")
            };
          })
        );
      });
    }
  }
});
