<template>
  <div>
    <transition name="slide">
      <div
        v-if="currentCategory !== categories.ALL"
        class="fin-masonry-indicator-placeholder"
      >
        <div
          v-if="currentCategory !== categories.ALL"
          class="fin-masonry-indicator"
        >
          <div
            class="content container"
            style="height: 100%;
  display: grid;"
          >
            <p
              style="color:white; cursor: pointer; margin: auto;"
              v-on:click="store.dispatch('resetCategory')"
            >
              CLEAR {{ currentCategory.description.toUpperCase() }} FILTER
            </p>
          </div>
        </div>
      </div>
    </transition>
    <isotope ref="isotope" :options="getOptions()" :list="projects">
      <div
        v-for="project in projects"
        :key="project.url"
        v-bind:class="{ 'fin-masonry-item': true }"
        v-on:click="router.push(project.url)"
      >
        <div class="fin-masonry-item-content">
          <v-lazy-image
            class="fin-masonry-image"
            :src="
              router.options.base + project.folder + '/' + project.tileImage
            "
          />
          <div class="fin-masonry-item-detail">
            <p
              class="title"
              style="font-weight: 700; font-size: 2rem; color: white;"
            >
              {{ project.title }}
            </p>
            <p
              class="subtitle"
              style="font-weight: 400; font-size: 0.7rem; color: white;"
            >
              {{ project.subtitle }}
            </p>
          </div>
        </div>
      </div>
    </isotope>
  </div>
</template>

<script>
import store from "../store";
import isotope from "vueisotope";
import { CATEGORY } from "../category";
import VLazyImage from "v-lazy-image";

export default {
  name: "Masonry",
  watch: {
    currentCategory: function(val) {
      this.applyCategoryToMasonry(val.description.toLowerCase());
    }
  },
  methods: {
    applyCategoryToMasonry: function(val) {
      this.$refs.isotope.filter(val);
    }
  },
  data: function() {
    var filterData = Object.values(store.state.categories).reduce(function(
      map,
      obj
    ) {
      map[obj.description.toLowerCase()] = function(itemElem) {
        return itemElem.categories.includes(obj.description.toLowerCase());
      };
      return map;
    },
    {});
    return {
      getOptions: function() {
        return {
          filter: this.currentCategory.description.toLowerCase(),
          hiddenStyle: {
            opacity: 0
          },
          visibleStyle: {
            opacity: 1
          },
          percentPosition: true,
          getFilterData: filterData
        };
      }
    };
  },
  computed: {
    store() {
      return store;
    },
    categories() {
      return CATEGORY;
    },
    currentCategory() {
      return store.state.currentCategory;
    },
    count() {
      return store.state.projects.length;
    },
    projects() {
      return store.state.projects;
    },
    router: function() {
      return this.$router;
    }
  },
  components: {
    isotope,
    VLazyImage
  }
};
</script>

