<template>
  <div>
    <transition name="fin-navigation-bar-handler-fade"
      ><div
        v-if="store.state.navigationBarExtended"
        class="fin-navigation-bar-handler"
        v-on:click="store.commit('toggleNavigationBar')"
      ></div>
    </transition>
    <div v-bind:class="barClassObject">
      <div class="fin-navigation-bar-header">
        <p
          class="fin-navigation-bar-title"
          style="cursor: pointer;"
          v-on:click="$router.push('/')"
        >
          {{ getTitleText }}
        </p>
        <div style="flex-grow:1;"></div>
        <transition name="fin-navigation-bar-content-fade">
          <div v-if="scrollToTopNeeded">
            <a v-on:click="this.scrollToTop" class="button is-text">
              <b-icon icon="format-vertical-align-top"></b-icon>
            </a>
          </div>
        </transition>
        <div style="flex-grow:0.02;"></div>
        <div>
          <a
            class="button is-text"
            v-on:click="store.commit('toggleNavigationBar')"
          >
            <b-icon
              v-bind:icon="store.state.navigationBarExtended ? 'close' : 'menu'"
            ></b-icon>
          </a>
        </div>
      </div>
      <transition name="fin-navigation-bar-content-fade">
        <div
          class="fin-navigation-bar-content"
          v-if="store.state.navigationBarExtended"
          style="flex-grow:1;"
        >
          <b>ABOUT</b>
          <p>
            <a href="https://fincher.im/" target="_blank">郑昊天</a> (ZHENG HAOTIAN / JustZht)
            's weird personal / team projects. For WIP projects, please visit my
            <a href="https://www.justzht.com" target="_blank">blog</a>. For
            company projects, please refer to the
            <a href="https://fincher.im/CV.html" target="_blank">resume</a>.
          </p>
          <div style="height: 1rem;"></div>
          <div v-if="isInHome">
            <b>FILTERS</b>
            <div
              v-bind:key="category.description"
              v-for="category in store.state.categories"
            >
              <a
                v-if="store.state.currentCategory !== category"
                v-on:click="store.commit('setCategory', category)"
              >
                ◦ {{ category.description }}
              </a>
              <b v-else>• {{ category.description }}</b>
            </div>
          </div>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import store from "../store";
export default {
  name: "NavigationBar",
  data: function() {
    return {
      scrollY: 0
    };
  },
  methods: {
    handleScroll() {
      this.scrollY = window.scrollY;
    },
    scrollToTop() {
      document.body.scrollTop = document.documentElement.scrollTop = 0;
    }
  },
  created() {
    window.addEventListener("scroll", this.handleScroll);
    // console.log("Listen Scroll");
  },
  destroyed() {
    // console.log("Don't Listen Scroll");
    window.removeEventListener("scroll", this.handleScroll);
  },
  computed: {
    scrollToTopNeeded() {
      return this.scrollY / window.innerHeight > 1;
    },
    store() {
      return store;
    },
    barClassObject: function() {
      return {
        "fin-navigation-bar": true,
        "fin-navigation-bar-in": false,
        box: true,
        "fin-navigation-bar-extend": store.state.navigationBarExtended
      };
    },
    getTitleText: function() {
      if (this.isInHome) {
        return "PORTFOLIO";
      } else {
        return "BACKTOHOME";
      }
    },
    isInHome: function() {
      return this.$router.currentRoute.path === "/";
    }
  }
};
</script>

