<template>
  <div class="fin-home">
    <NavigationBar />
    <NavigationContent>
      <Masonry />
      <Footer />
    </NavigationContent>
  </div>
</template>

<script>
// @ is an alias to /src
import NavigationBar from "../components/NavigationBar.vue";
import NavigationContent from "../components/NavigationContent.vue";
import Masonry from "../components/Masonry.vue";
import Footer from "../components/Footer.vue";

export default {
  name: "Home",
  components: {
    NavigationBar,
    NavigationContent,
    Masonry,
    Footer
  },
  methods: {
    getDesc() {
      return "Portfolio from 郑昊天 ZHENG HAOTIAN (aka Justin Fincher / JustZht)";
    },
    getTitle() {
      return "JustZht's Portfolio";
    },
    getBannerImageLink() {
      return this.router.options.base + "banner.jpg";
    }
  },
  computed: {
    router: function() {
      return this.$router;
    },
    route: function() {
      return this.$route;
    }
  },
  mounted: () => {
    document.dispatchEvent(new Event("render-event"));
  },
  head: {
    title: function() {
      return {
        inner: this.getTitle(),
        separator: ""
      };
    },
    meta: function() {
      return [
        {
          name: "description",
          content: this.getDesc()
        },
        {
          name: "twitter:title",
          content: this.getTitle()
        },
        { name: "twitter:creator", content: "@JustZht" },
        { name: "twitter:site", content: "@JustZht" },
        {
          name: "twitter:description",
          content: this.getDesc()
        },
        { name: "twitter:card", content: "summary_large_image" },
        {
          name: "twitter:image",
          content: this.getBannerImageLink()
        },
        {
          name: "twitter:image:src",
          content: this.getBannerImageLink()
        },
        {
          property: "og:type",
          content: "website"
        },
        {
          property: "og:site_name",
          content: this.getDesc()
        },
        {
          property: "og:title",
          content: this.getTitle()
        },
        {
          property: "og:description",
          content: this.getDesc()
        },
        {
          property: "og:image",
          content: this.getBannerImageLink()
        }
      ];
    }
  }
};
</script>

<style scoped>
.fin-home {
  position: absolute;
  width: 100%;
  height: 100%;
}
</style>
